import React from 'react'
import PrivateRoute from './PrivateRoute'
import {Route,Redirect} from 'react-router-dom';

function RouteWithSubRoutes(route) {
    if(route.pri){
        return (
            <PrivateRoute
                path={route.path}
                component={route.component}
                routes={route.routes}
            />
        )
    }
    return (
        <Route
            path={route.path}
            exac={route.exac}
            component={route.component}
            routes={route.routes}
        />
    )
}

export default RouteWithSubRoutes
